@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville:ital@1&display=swap);
.App {
  display:block;
  align-items:center;
  justify-content:center;
  background-color : #D5AF77;
  height : 100vh;
  widht : 100vh;
  padding: 3px;
}

.Header{
  text-align: center;
}

.Play-ground{
  justify-content: center;
  background : #0C750F;
  width : 80%;
  height: 40%;
  margin-left: 10%;
  margin-right: 10%;
  margin-bottom: 5%;
  box-shadow: 5px 5px 8px black;
  border-radius: 15px;
}


.NumberField-container {
  position: relative;
  top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 20%;
}


.Dice-container{
  display:flex;
  justify-content: center; 
  align-items : center;
  margin-top: 22vh;
  height: 10%;
}

.button-container{
  display:flex;
  justify-content: center;
 
}

button.roll{
  font-size: 2vh;
  font-family : "Quicksand";
  font-weight: bold;
  cursor: pointer;
  border-radius : 2px;
 
}


button.startover{
  margin-left : 20%;
  font-size: 2vh;
  font-family : "Quicksand";
  font-weight: bold;
  cursor: pointer;
  border-radius : 2px;
}


.NumberField {
  background: #6D0D3B;
  width: 8%;
  height: 100%;
  cursor: pointer;
  border-radius: 10px;
  box-shadow:  1px 1px 10px black;
  margin: 5px;
  display: flex;
  justify-content: center;
  align-item: center;
}

h1.title{
  font-family: 'Libre Baskerville', serif;
  color: white;
  text-shadow: 4px 4px 2px #9CA9A9;

}

div.number{
  position : relative;
  top: 30%;
  font-family: 'Libre Baskerville', serif;
  color: white;
  font-size: 3vh;
  text-shadow: 2px 2px  3px black;
}

img.Die{
  margin: 10px;
  height : 100%;
  width: auto;
  box-shadow: 5px 5px 8px black;
}

/* little hack to restart the animation 
   just define the same animation twice with a different name.
   Run the "to" animation first and then the "back" animation as 
   reverse
*/

@keyframes front-flip {
  0%   {transform: rotateX(0deg); transform-origin: bottom}
  100%  {transform: rotateX(-150deg); transform-origin: bottom}
}

@keyframes back-flip {
  0%   {transform: rotateX(0deg); transform-origin: bottom}
  100%  {transform: rotateX(-150deg); transform-origin: bottom}
}


@keyframes shake {
  0% { transform: translate(1px, 1px) rotate(0deg); }
  10% { transform: translate(-1px, -2px) rotate(-1deg); }
  20% { transform: translate(-3px, 0px) rotate(1deg); }
  30% { transform: translate(3px, 2px) rotate(0deg); }
  40% { transform: translate(1px, -1px) rotate(1deg); }
  50% { transform: translate(-1px, 2px) rotate(-1deg); }
  60% { transform: translate(-3px, 1px) rotate(0deg); }
  70% { transform: translate(3px, 1px) rotate(-1deg); }
  80% { transform: translate(-1px, -1px) rotate(1deg); }
  90% { transform: translate(1px, 2px) rotate(0deg); }
  100% { transform: translate(1px, -2px) rotate(-1deg); }
}





